.mas-video-container {
  box-shadow: none; }
  .mas-video-container .modal-content {
    background-color: transparent;
    display: flex;
    align-items: center; }
  .mas-video-container .modal-header {
    border: none;
    align-self: flex-end; }
    .mas-video-container .modal-header span {
      color: #00D7AF;
      text-shadow: none;
      font-size: 36px; }
  .mas-video-container iframe {
    max-width: 100%; }
  .mas-video-container .spinner-animation {
    z-index: -1; }
    .mas-video-container .spinner-animation .dot1,
    .mas-video-container .spinner-animation .dot2 {
      background-color: #00D7AF !important; }
