.landing-mas-container {
  min-height: 700px;
  position: relative;
  background-color: white;
  overflow: hidden; }
  @media screen and (min-width: 576px) {
    .landing-mas-container {
      min-height: 900px; } }
  @media screen and (min-width: 992px) {
    .landing-mas-container {
      min-height: 700px; } }
  @media screen and (max-width: 991px) {
    .landing-mas-container {
      margin-bottom: 30px; } }
  .landing-mas-container .landing-mas-inner-container {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
    margin: auto; }
    @media screen and (max-width: 991px) {
      .landing-mas-container .landing-mas-inner-container {
        padding: 0 15px; } }
    .landing-mas-container .landing-mas-inner-container .mas-info-container {
      position: relative;
      z-index: 1; }
      @media screen and (max-width: 991px) {
        .landing-mas-container .landing-mas-inner-container .mas-info-container {
          display: flex;
          flex-direction: column;
          justify-content: center;
          align-items: center;
          text-align: center; }
          .landing-mas-container .landing-mas-inner-container .mas-info-container h2 {
            max-width: 450px; } }
      .landing-mas-container .landing-mas-inner-container .mas-info-container h2 {
        margin-top: 45px;
        font-weight: 800;
        font-size: 30px;
        color: #6480EB; }
        @media screen and (max-width: 575px) {
          .landing-mas-container .landing-mas-inner-container .mas-info-container h2 {
            font-size: 18px; } }
    .landing-mas-container .landing-mas-inner-container .mas-video-container {
      display: flex;
      justify-content: center;
      align-items: center; }
      .landing-mas-container .landing-mas-inner-container .mas-video-container button {
        transition: transform 0.3s ease; }
        .landing-mas-container .landing-mas-inner-container .mas-video-container button:hover {
          transform: scale(1.1); }
      .landing-mas-container .landing-mas-inner-container .mas-video-container .curved-box {
        max-width: 487px;
        width: 100%;
        height: auto;
        transform: translateY(30px); }
      .landing-mas-container .landing-mas-inner-container .mas-video-container .mas-bg {
        width: 150%;
        max-width: 100vw;
        position: absolute;
        right: 0;
        left: 0;
        margin: auto;
        bottom: 0;
        transform: translateX(-21%) translateY(15%); }
        @media screen and (max-width: 991px) {
          .landing-mas-container .landing-mas-inner-container .mas-video-container .mas-bg {
            width: 100%;
            transform: translateY(10%) translateY(15%); } }
        @media screen and (max-width: 575px) {
          .landing-mas-container .landing-mas-inner-container .mas-video-container .mas-bg {
            width: 150%;
            transform: translateY(10%); } }
